.section-about {
  background-color: $color-grey-light1;
  padding: 25rem 0;
  margin-top: -20vh;
}

.section-features {
  padding: 20rem 0;
  background-image: linear-gradient(
      to right bottom,
      rgba($color-primary-light, 0.781),
      rgba($color-primary-dark, 0.8)
    ),
    url("../img/nat-4.jpg");
  background-size: cover;
  margin-top: -10rem;
  transform: skewY(-7deg);

  // select all direct children
  & > * {
    transform: skewY(7deg);
  }
}

.section-tours {
  background-color: $color-grey-light1;
  margin-top: -10rem;
  padding: 25rem 0 15rem 0;
}

.section-stories {
  padding: 15rem 0;
  position: relative;
}
